//import * as _ from 'lodash';

export default class Song {
    private _id: string;
    private _name: string;

    public constructor(id: string, name: string) {
        if (!id || id === '')
            throw new Error('id is required.');

        if (!name || name === '')
            throw new Error('name is required.')

        this._id = id;
        this._name = name;
    }
}
